import * as React from "react"
import {useParams} from 'react-router-dom'
import getData from "../helpers/fetchData"
import {Link} from 'react-router-dom'
import '../../styles/apost.css'

function Apost(){

    let {id} = useParams()
    const [post, setPost] = React.useState({})

    const handlePost = async() =>{
        let post = await getData(`posts/${id}`)
        setPost(post)

        console.log(post)
    }

    React.useEffect(() => {
        handlePost()
    }, [])

    return(
        <div className="container">
            <nav style={{'background': 'crimson', 'display': 'flex', 'margin-bottom': '10px', 'padding': '10px', 'width': '100vw'}}>
                <Link to="/" style={{'margin': '10px', 'color': '#fff'}}>Home</Link>
                <Link to="/posts" style={{'margin': '10px', 'color': '#fff'}}>Posts</Link>
                <Link to="/todos" style={{'margin': '10px', 'color': '#fff'}}>Todos</Link>
            </nav>
            {
                (!post.id) ? <h1>Loading ...</h1>:
                    <div className="container2">
                        <h1 id="title">{post.title}</h1>
                        <p>Posted on: {post.date} by: {post.author}</p>
                        <p>{post.body}</p>
                        <img src={post.img} alt={post.description} />
                    </div>
            }

        </div>
    )
}

export default Apost