// import React, {useState, useEffect} from 'react'
import * as React from 'react'
import { Link } from 'react-router-dom'
import getData from '../helpers/fetchData'

function Todos() { //Opening a component

    const [error, setError] = React.useState('')
    const [todos, setTodos] = React.useState([])

    const handleTodos = async() => {
        setTodos([])
        setError('')

        const todos = await getData()
        if(todos?.error !== undefined) return setError(todos.error)

        setTodos(todos)
    }

    React.useEffect(() => { //Is invoked at component rendering
        handleTodos()

    }, [])

    const handleClick = () => handleTodos()

    return (
        <div className="container mx-auto">
            <nav style={{'background': 'crimson', 'display': 'flex', 'margin-bottom': '10px', 'padding': '10px', 'width': '100vw'}}>
                <Link to="/" style={{'margin': '10px', 'color': '#fff'}}>Home</Link>
                <Link to="/posts" style={{'margin': '10px', 'color': '#fff'}}>Posts</Link>
                <Link to="/todos" style={{'margin': '10px', 'color': '#fff'}}>Todos</Link>
            </nav>
            
            <h1>Todos</h1>
            {error}
            <div>
                <button onClick={handleClick}>Get Todos</button>
            

            {todos?.length <= 0 && <div>Loading...</div>}
            {todos?.length > 0 &&
                <table>
                    <tr>
                        <th>To Do</th>
                        <th>Status (complete)</th>
                    </tr>
                    <tr>
                        <td>{todos.map(todo => <li key={todo.id}>{todo.title}</li>)}</td>
                        <td>{todos.map(todo => <li>{todo.completed ? 'Yes':'No'}</li>)}</td>
                    </tr>
                        
                </table>

                // <ul>
                //     {todos.map(todo => <li key={todo.id}>{todo.title},  {
                //         todo.complete? 'completed: yes':'completed: no'
                //     }</li>)}
                // </ul>
            }
            </div>
        </div>
    )
}

export default Todos