import * as React from 'react'
import {Link} from 'react-router-dom'

function Home() {
    
    return (
        <div className="">

            <nav style={{'background': 'crimson', 'display': 'flex', 'margin-bottom': '10px', 'padding': '10px', 'width': '100vw'}}>
                <Link to="/" style={{'margin': '10px', 'color': '#fff'}}>Home</Link>
                <Link to="/posts" style={{'margin': '10px', 'color': '#fff'}}>Posts</Link>
                <Link to="/todos" style={{'margin': '10px', 'color': '#fff'}}>Todos</Link>
            </nav>


            <h1>Use the name above</h1>
        </div>
    )
}

export default Home