// import React, {useState, useEffect} from 'react'
import * as React from 'react'
import {Link} from 'react-router-dom'
import getData from '../helpers/fetchData'
import '../../styles/posts.css'

function Posts() { //Opening a component

    const [error, setError] = React.useState('')
    const [posts, setPosts] = React.useState([])

    const handlePosts = async () => {
        setPosts([])
        setError('')

        const posts = await getData("posts")
        if(posts?.error !== undefined) return setError(posts.error)

        setPosts(posts)
    }

    React.useEffect(() => { //Is invoked at component rendering
        handlePosts()
    }, [])

    
    const handleClick = () => handlePosts()

    return (
        <>
            <nav style={{'background': 'crimson', 'display': 'flex', 'margin-bottom': '10px', 'padding': '10px', 'width': '100vw'}}>
                <Link to="/" style={{'margin': '10px', 'color': '#fff'}}>Home</Link>
                <Link to="/posts" style={{'margin': '10px', 'color': '#fff'}}>Posts</Link>
                <Link to="/todos" style={{'margin': '10px', 'color': '#fff'}}>Todos</Link>
            </nav>
            
            <h1 id="title">My Posts</h1>
            {error}
            <div className="container2">
                <button className="btn" onClick={handleClick}>Get Posts</button>

                {posts?.length <= 0 && <div>Loading...</div>}
                {posts?.length > 0 &&
                    <ul>
                        {posts.map(post => 
                            
                                <li key={post.id}>
                                    <Link key={post.id} to={`posts/${post.id}`}>
                                        <h1>{post.title}</h1>
                                    </Link>
                                </li>
                            )}
                    </ul>
                }
            </div>
        </>
    )
}

export default Posts